  --info-title-cl: #8c8c8c;
  --text-footer-cl: #545454;
  --main-bg-cl: #fff;
  --secondary-bg-cl: #f7f7f7;
  --dark-bg-cl: #2f2f2f;
  --dark-secondary-bg-cl: #272727;
  --dark-modal-bg-cl: #2f2e2e;
  --secondary-accent-cl: #ff6b08;
  --transition-cubic: cubic-bezier(.4, 0, .2, 1);
  --transition-time: 500ms;
  --dark-icon-color: #fff;
  --accent-btn-color: #ff6b08;
  --white-btn-color: #fff;
  --black-btn-color: #000;
