  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
}

.inner.one {
  border-bottom: 3px solid var(--secondary-accent-cl);
  animation: rotate-one 1s linear infinite;
  top: 0%;
  left: 0%;
}

.inner.two {
  border-right: 3px solid var(--secondary-accent-cl);
  animation: rotate-two 1s linear infinite;
  top: 0%;
  right: 0%;
